import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
export default {
    data () {
        return {
           value1: '',
           page: 1,
           pagesize: 10,
           items: [],
           pagetotal: 0,
           date_type: 'all',
           amount_total: '',
           content: '正在加载...',
           current:''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleCurrentChange (val){
            this.page = val
            let that = this
            if(this.value1 == ""){
                this.http(this.configs.apiUl + "/user/amountrecord/giftIncreaseDot?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.record_list.items
                        that.pagetotal = datas.d.record_list.total
                        that.amount_total = datas.d.amount_total
                    }  
                })
            }else{
                this.http(this.configs.apiUl + "/user/amountrecord/giftIncreaseDot?access_token="+this.$store.state.access_token+"&date="+this.value1+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.record_list.items
                        that.pagetotal = datas.d.record_list.total
                        that.amount_total = datas.d.amount_total
                    }  
                })
            }
        }
    },
    watch: {
        value1 () {
          var date = this.value1;
        //   date = date.substring(0,19);    
        //   date = date.replace(/-/g,'/'); 
        //   var timestamp = new Date(date).getTime() /1000;//转换时间戳
          let that = this
          that.content = '正在加载...'
          this.page = 1
            this.http(this.configs.apiUl + "/user/amountrecord/giftIncreaseDot?access_token="+this.$store.state.access_token+"&date="+this.value1+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.record_list.items
                    that.pagetotal = datas.d.record_list.total
                    that.amount_total = datas.d.amount_total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }  
            })
        }
    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/user/amountrecord/giftIncreaseDot?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
            var datas = res.data
            if(datas.c == 0){
                that.items = datas.d.record_list.items
                that.pagetotal = datas.d.record_list.total
                that.amount_total = datas.d.amount_total
                if(that.items.length == 0){
                    that.content = '暂无数据'
                }
            }  
        })
    },
    mounted () {
        var days =  new Date()
        this.current = days.getFullYear() + "-" + (days.getMonth() + 1 ) + "-" + days.getDate();
        console.log(this.current)
    }
}



