import $ from 'jquery'
export default {
    data () {
        return {
            content: ''
        }
    },
    components: {

    },
     methods: {

    },
    created(){
        let that = this
        //获取列表信息
        this.http(this.configs.apiUl+"/user/app/privacyPolicy?format=json",'get','',function(res){
            let datas= res.data
            that.content = datas.d.content
        })                    
    }
}



