import vHeader from './public/header'
import vFooter from './public/footer'
export default {
    data () {
        return {
            user: ""
        }
    },
    components: {
        vHeader,
        vFooter
    },
   created() {
       let that=this
       this.http(this.configs.apiUl+'/user/app/registerAgreement?format=json&'+this.configs.display,'get','',function(res){
           console.log(res)
           let datas=res.data
           if(datas.c==0){
              that.user = datas.d.content
           }
       })
   }
}



