import { Message } from 'element-ui'
import vHeader from './public/header'
import vVideo from './public/video'
import Item from './public/item'
import vFooter from './public/footer'
export default {
    data () {
        return {
            anchorid: '',
            anchor: {},
            user: {},
            live:{},
            expre: false,
            anchorData: {},
            gift: [],
            global: [],
            isModals: false,
            userid: ''
        }
    },
    components: {
        vHeader,
        vVideo,
        Item,
        vFooter
    },
    methods: {
        giftFn () {
            this.$store.state.isgift = false
        },
        liveRoom () {  
            //观众进入直播间
            let that = this
            this.http(this.configs.apiUl+"/live/room/enter?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&anchor_user_id="+this.anchorid,'get','',function(res){
                let datas = res.data
                // localStorage.setItem('refresh',"")
                console.log(datas)
                if(datas.c==0){
                    // that.anchor = datas.d.anchor
                    that.user = datas.d.user
                    that.live = datas.d.live
                    if(Number(that.anchor.anchor_ranking) > 200){
                        that.anchor.anchor_ranking = 200 + '+'
                    }
                }else{
                    if(datas.c == 10041){//判断是否当前用户被踢出房间
                        that.$confirm(datas.m, {
                            title: '您已被踢出房间',
                            confirmButtonText: '确定',
                            closeOnClickModal: false,
                            showClose: false,
                            center: true,
                            showCancelButton: false
                            }).then(() => {
                                that.$router.push({path:'/'})
                            }).catch(() => {

                        });
                    }else if(datas.c == 10008){
                        that.$confirm(datas.m, {
                            // title: '主播被禁播',
                            confirmButtonText: '确定',
                            closeOnClickModal: false,
                            showClose: false,
                            center: true,
                            showCancelButton: false
                            }).then(() => {
                                that.$router.push({path:'/'})
                            }).catch(() => {

                        });
                    }
                }
            })
        }
    },
    created () {
        this.anchorid = this.$route.query.id//获取主播id
        let that = this
        //验证是否登录
        let access_token = localStorage.getItem('access_token')
        if(access_token == '' || access_token == "undefined"){//未登录状态
            //获取主播信息
            this.http(this.configs.apiUl+"/live/anchor/getInfo?user_id="+this.anchorid,'get','',function(res){
                let datas = res.data
                if(datas.c==0){
                    console.log(datas)
                    that.anchorData = datas.d.anchor
                    that.anchorData.user_id = that.anchorid
                    that.userid = that.anchorid
                    that.anchorData.is_follow = false
                    if(that.anchorData.user_sex == '1'){
                        that.anchorData.user_sex = "/static/images/nan.png"
                    }else if(that.anchorData.user_sex == '2'){
                        that.anchorData.user_sex = "/static/images/nv.png"
                    }
                }
            })
        }else{//登录状态
            //获取主播信息
            this.http(this.configs.apiUl+"/live/anchor/getAnchorInfo?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.anchorid,'get','',function(res){
                let datas = res.data
                if(datas.c==0){
                    that.anchorData = datas.d
                    that.userid = datas.d.user_id
                    if(that.anchorData.user_sex == '1'){
                        that.anchorData.user_sex = "/static/images/nan.png"
                    }else if(that.anchorData.user_sex == '2'){
                        that.anchorData.user_sex = "/static/images/nv.png"
                    }
                }
            })
        }
        //获取礼物信息
        this.http(this.configs.apiUl+"/live/gift/index?access_token="+this.$store.state.access_token+'&'+this.configs.display,'get','',function(res){
            var datas = res.data
            if(datas.c == 0){
                that.gift = datas.d.gift
            }
        })
        //获取全站直播
        this.http(this.configs.apiUl+"/pc/app/getLatest?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&pagesize=30",'get','',function(res){
            var datas = res.data
            if(datas.c == 0){
                that.global = datas.d.items
            }
        })
    }
}



