import Vuex from 'vuex'
import register from '../src/components/user/register'
import seekPwd from '../src/components/user/seekPwd'
export default {
  name: 'app',
  data () {
    return {
      
    }
  },
  components: {
    register,
    seekPwd
  },
  computed: {
    isRegister () {
     return this.$store.state.isRegister
    },
    isseekPwd () {
      return this.$store.state.isseekPwd
    }
  },
  created () {
        //验证是否登录
        let access_token = localStorage.getItem('access_token')
        let token = this.$route.query.token 
        if(access_token == '' || access_token == "undefined"){
            if(token == undefined || token == ""){
              //获取游客账号
              let that = this
              this.http(this.configs.apiUl + "/account/register/getSign","get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                      let loginInfo = {
                            identifier: datas.d.account,
                            userSig: datas.d.sign,
                            sdkAppID: datas.d.app_id,
                            appIDAt3rd: datas.d.app_id,
                            accountType: datas.d.account_type,
                        }
                        localStorage.setItem('userSign', JSON.stringify(loginInfo))
                    }
              })
            }else{
                localStorage.setItem('access_token', token)
                this.$store.state.access_token = token
            }
        }else{
            if(access_token == 'otherLogin'){//判断用户第三方登录
                if(token.constructor==Array){//判断token是否为数组
                  localStorage.setItem('access_token', token[token.length-1])
                  this.$store.state.access_token = token[token.length-1]
                }else{
                  localStorage.setItem('access_token', token)
                  this.$store.state.access_token = token
                }
            }else{
                this.$store.state.access_token = access_token
            }
            this.$store.commit('hidwRegister')//隐藏登录框
            //验证登录状态
             this.http(this.configs.apiUl + "/pc/login/status?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
               let datas = res.data
                if(datas.c == 0){
                  
                }else{
                    localStorage.setItem('access_token','')//localStorage清除用户登录信息
                    that.$store.state.access_token = ""//vuex清除用户登录信息
                    // that.$store.commit('showRegister')//显示登录框
                }
             })
        }
  }
}



