import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import { Message } from 'element-ui'
export default {
    data () {
        return {
            nowBind:false,
            isrelieve:false,
            change:false,
            after:false,
            unbound: true,
            num: 60,
            phone: '',
            iscode: true,
            codenum: '',
            password: '',
            changePhone: '',
            newcode: '',
            Interval: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        code () {
            let that = this
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空！',
                    type: "error"
                })
                return false
            }
            if(this.iscode){
                this.http(this.configs.apiUl + "/account/verifycode/bindPhone?access_token="+this.$store.state.access_token+"&phone="+this.phone+"&"+this.configs.display,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.iscode = false
                            Message({
                            message: '发送成功!',
                            type: 'success'
                        })
                        var Interval = setInterval(function(){
                            that.num -= 1
                            if(that.num <= 0){
                                Message({
                                    message: '未收到验证码？点击重新发送'
                                })
                                that.num = 60
                                that.iscode = true
                                clearInterval(Interval)
                            }
                        },1000)
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                })
            }
        },
        changeCode () {
            let that = this
            if(this.iscode){
                this.http(this.configs.apiUl + "/account/verifycode/changePhone?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.iscode = false
                            Message({
                            message: '发送成功!',
                            type: 'success'
                        })
                        that.Interval = setInterval(function(){
                            that.num -= 1
                            if(that.num <= 0){
                                Message({
                                    message: '未收到验证码？点击重新发送'
                                })
                                that.num = 60
                                that.iscode = true
                                clearInterval(that.Interval)
                            }
                        },1000)
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                })
            }
        },
        submit () {//绑定手机号
            let that = this
            if(this.password == ''){
                Message({
                    message: '密码不能为空！',
                    type: 'error'
                })
                return
            }else if(this.codenum == ''){
                Message({
                    message: '验证码不能为空！',
                    type: 'error'
                })
                return
            }
            this.http(this.configs.apiUl + "/user/bind/phone?access_token="+this.$store.state.access_token+"&password="+this.password+"&phone="+this.phone+"&code="+this.codenum+"&"+this.configs.display,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    Message({
                        message: '绑定成功',
                        type: 'success'
                    })
                    this.$router.push({path:'/set'})
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        ensure () {//更换手机验证码
            if(this.codenum == ""){
                Message({
                    message: '验证码不能为空！',
                    type: "error"
                })
                return false
            }
            let that = this
            this.http(this.configs.apiUl + "/account/verifycode/judgeChangePhone?access_token="+this.$store.state.access_token+"&code="+this.codenum+"&"+this.configs.display,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    clearInterval(that.Interval)
                    that.num = 60
                    // console.log(that.num)
                    that.iscode = true
                    that.change = false
                    that.after = true  
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        sure () {//更换绑定
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空！',
                    type: "error"
                })
                return false
            }else if(this.newcode == ""){
                Message({
                    message: '验证码不能为空！',
                    type: "error"
                })
                return false
            }
            let that = this
            this.http(this.configs.apiUl + "/user/bind/changePhone?access_token="+this.$store.state.access_token+"&new_phone="+this.phone+"&code="+this.codenum+"&new_phone_code="+this.newcode+"&"+this.configs.display,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    Message({
                        message: '更换成功',
                        type: 'success'
                    })
                    this.$router.push({path:'/set'})
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        relieve (){//更换绑定
            this.change = true
            this.unbound = false
        }
    },
    created () {
        let state = this.$route.query.state
        this.changePhone = this.$route.query.phone
        if(state == 'verified'){
            this.isrelieve = true
        }else if(state == 'unverified'){
            this.nowBind = true
        }
    }
    
}



