import qs from 'qs'
import vHeader from './public/header';
import Item from './public/item';
import vFooter from './public/footer'
export default {    
    data () {
        return {
            activeName: '',
            live_category:[],
            items:[],
            pagetotal: 0 ,
            pagesize:20,
            page: 1,
            content: '正在加载...'
        }
    },
    components: {
      vHeader,
      Item,
      vFooter
    },
    methods: {
        handleClick(tab,event) {
            this.items = []
            let that = this
            that.content = '正在加载...'
            that.page = 1
            this.http(this.configs.apiUl+"/pc/app/liveByCate?access_token="+this.$store.state.access_token+"&category_id="+this.activeName+"&page="+this.page+"&pagesize="+this.pagesize+'&'+this.configs.display,'post','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.items
                    that.pagetotal = datas.d.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }
            })
        },
        handleCurrentChange(val){//分页
            this.page = val
            let that = this
            this.http(this.configs.apiUl+"/pc/app/liveByCate?access_token="+this.$store.state.access_token+"&category_id="+this.activeName+"&page="+ this.page +"&pagesize="+this.pagesize+'&'+this.configs.display,'post','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    console.log(datas.d.items)
                    that.items = datas.d.items
                }
            })
        },
        videoList(){
            let that = this
            this.http(this.configs.apiUl+"/pc/app/liveByCate?access_token="+this.$store.state.access_token+"&category_id="+this.activeName+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,'post','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.items
                    that.pagetotal = datas.d.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }
            })
        }
    },
    created(){
       let that = this
       this.http(this.configs.apiUl+"/pc/app/liveclass?access_token="+this.$store.state.access_token+"&"+this.configs.display,'get','',function(res){
            var datas = res.data
            if(datas.c == 0){
                that.live_category = datas.d.live_category
                that.activeName = that.live_category[0].anchor_category_id 
                that.videoList()     
            }
       })
    }
}



