import { Message } from 'element-ui'
import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
export default {
    data () {
        return {
           page: 1,
           pagesize: 7,
           items: [],
           pagetotal: '',
           content: '正在加载...'
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleCurrentChange (val){//分页
            this.content = '正在加载...'
            this.page = val
            let that = this
            this.http(this.configs.apiUl + "/live/roomadmin/index?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.anchor_admin.items
                }     
            })
        },
        del (id){//删除房管
            let that = this
            this.http(this.configs.apiUl + "/live/roomadmin/delete?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+id,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                   Message({
                        message: '取消成功！',
                        type: 'success'
                    })
                    if(that.items.length <= 1){
                        that.page = that.page-1
                        if(that.page >=0){
                            that.page = 1
                        }
                    }
                    that.http(that.configs.apiUl + "/live/roomadmin/index?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&page="+that.page+"&pagesize="+that.pagesize,"get","",function(res) {//获取用户信息
                        var datas = res.data
                        if(datas.c == 0){
                            that.items = datas.d.anchor_admin.items
                            if(that.items.length == 0){
                                that.content = '暂无数据'
                            }
                        }     
                    })
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })

        }
    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/live/roomadmin/index?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
            var datas = res.data
            if(datas.c == 0){
                that.items = datas.d.anchor_admin.items
                that.pagetotal = datas.d.anchor_admin.total
                if(that.items.length == 0){
                    that.content = '暂无数据'
                }
            }     
        })
    }
}



