import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import qs from 'qs'
export default {
    data () {
        return {
           userid: '',
           page: 1,
           pagesize: 8,
           items: [],
           pagetotal: 0,
           isphone: "",
           content: '正在加载...',
           isuser: false,
           anchor_is_live: 'N',
           user: {},
           anchor_id: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleCurrentChange (val){//分页
            this.page = val
            let that = this
            if(this.isuser){
                this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+ val +"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.videos.items
                    }     
                })
            }else{
                this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&user_id="+this.userid+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.videos.items
                        that.pagetotal = datas.d.videos.total
                    }     
                })
            }
        },
        go () {
            if(this.userid == this.$store.state.user_id){
                this.$router.push({path:'/anchorLive'})
            }else{
                 this.$router.push({path:'/liveRoom',query:{'id':this.userid}})
            }
        }
    },
    created () {
        this.userid = this.$route.query.uid//获取用户id
        if(this.userid != undefined){
            this.isuser = false
            let that = this
            //获取用户回放
            this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&user_id="+this.userid+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                var datas = res.data
                if(datas.c == 0){
                    that.anchor_is_live = datas.d.anchor_is_live
                    that.anchor_id = datas.d.anchor_id
                    that.items = datas.d.videos.items
                    that.pagetotal = datas.d.videos.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }     
            })
            //获取个人信息
            this.http(this.configs.apiUl + "/user/profile/card?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.userid,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                }
            })
        }else{
            this.isuser = true
            let that = this
            //获取主播回放
            this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.videos.items
                    that.pagetotal = datas.d.videos.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }     
            })
            this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.user = datas.d                    
                }     
            })
        }
    }
}



