import vHeader from './public/header'
import vFooter from './public/footer'
export default {
    data () {
        return {
            userid:'',
            detail:{},
            reason:false,
            report:[],
        }
    },
    components: {
        vHeader,
        vFooter,
    },
     methods: {
        attention(){//关注
            let that = this
            this.http(this.configs.apiUl+"/user/follow/add?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&user_id='+this.detail.user_id,'get','',function(res){
                let datas = res.data
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: '关注成功!'
                    })
                    that.detail.is_follow = true
                }else if(datas.c == 10002){
                    that.$message({
                        // type: 'error',
                        message: '不能关注自己哦~'
                    })
                } else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    })
                }
            })
        },
        unattention(){//取消关注
            let that = this
            this.http(this.configs.apiUl+"/user/follow/delete?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&user_id='+this.detail.user_id,'get','',function(res){
                let datas = res.data
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: '取消成功!'
                    })
                    that.detail.is_follow = false
                }else {
                    that.$message({
                        type: 'error',
                        message: datas.m
                    })
                }
            })
        },
        informm(){
            if(this.reason){
                this.reason = false
            }else{
                this.reason = true
            }
        },
        //举报
        reportly(content){
            let that = this
            this.http(this.configs.apiUl+"/live/report/room?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.userid+"&content="+content,'get','',function(res){
                let datas = res.data
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: '举报成功'
                    })
                    that.reason = false
                }else{
                    that.$message({
                        type:'error',
                        message: datas.m
                    })
                }
            })
        },
        formatSeconds (value) {
             function zeroize( num ) {
                return (String(num).length == 1 ? '0' : '') + num;
            }
            var theTime = parseInt(value);// 秒 
            var theTime1 = 0;// 分 
            var theTime2 = 0;// 小时 
            if(theTime > 60) { 
            theTime1 = parseInt(theTime/60); 
            theTime = parseInt(theTime%60); 
            if(theTime1 > 60) { 
                theTime2 = parseInt(theTime1/60); 
                theTime1 = parseInt(theTime1%60); 
                } 
            } 
            var result = "00 : "+zeroize(theTime); 
            if(theTime1 > 0) { 
                result = zeroize(theTime1)+" : "+zeroize(theTime); 
             } 
            if(theTime2 > 0) { 
                result = " "+zeroize(theTime2)+" : "+zeroize(theTime1)+" : "+zeroize(theTime); 
            }
            
            return result; 
        } 
    },
    created(){
        let that = this
        this.userid = this.$route.query.id
        this.http(this.configs.apiUl+"/video/app/playBackDetail?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&id='+this.userid,'get','',function(res){
            let datas = res.data
            if(datas.c == 0){
                that.detail = datas.d
            //    datas.d.url = 'http://1255530974.vod2.myqcloud.com/ca3bc4e8vodgzp1255530974/75a4a7e94564972819125470244/playlist.m3u8'
            console.log(datas.d.url)
            console.log(111111111)
                var player = new TcPlayer('id_test_video', {
                    "m3u8": datas.d.url, //请替换成实际可用的播放地址
                    "flv": datas.d.url,
                    "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                    "coverpic" : "",
                    "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                    "height" : '100%'//视频的显示高度，请尽量使用视频分辨率高度
                });
                if(that.detail.sex == '1'){
                    that.detail.sex = "/static/images/nan.png"
                }else if(that.detail.sex == '2'){
                    that.detail.sex = "/static/images/nv.png"
                }
            }
        })
        //举报列表
        this.http(this.configs.apiUl+"/live/report/index?access_token="+this.$store.state.access_token+'&'+this.configs.display,'get','',function(res){
            let datas = res.data 
            if(datas.c == 0){
                that.report = datas.d.report
            } 
        })
    }
}



