import vHeader from './public/header'
import vFooter from './public/footer'
export default {
    data () {
        return {
             activeNames: '1',
             ques: []
        }
    },
    components: {
        vHeader,
        vFooter
    },
    methods: {
        handleChange(val) {
            console.log(val)
        }
    },
    created(){
        let that=this
        this.http(this.configs.apiUl+'/user/help/hotQuestion?format=json&'+this.configs.display,'get','',function(res){
            let datas=res.data
            if(datas.c == 0){
                that.ques = datas.d.questions
            }
        })
    }
}



