//全局过滤器
Vue.filter( 'levelcolor', function(value) {
  if(value >=100){
    return 'level10';
  }else if(value >=90){
    return 'level9';
  }else if(value >=80){
    return 'level8';
  }else if(value >=70){
    return 'level7';
  }else if(value >=60){
    return 'level6';
  }else if(value >=50){
    return 'level5';
  }else if(value >=40){
    return 'level4';
  }else if(value >=30){
    return 'level3';
  }else if(value >=20){
    return 'level2';
  }else if(value >=0){
    return 'level1';
  }
});
//全局过滤器
Vue.filter( 'time', function(value) {
  function zeroize( num ) {
    return (String(num).length == 1 ? '0' : '') + num;
  }
  var tmDate = new Date( value * 1000 );  // 参数时间戳转换成的日期对象

  var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
  var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();

  return  Y + '-'+ zeroize(m) + '-' + zeroize(d) + '    ' + zeroize(H) + ':' + zeroize(i) + ':' + zeroize(s);

});

