export default {
    props: ['index','name'],
    data () {
        return {
            iinfor:[
                {ccon:'用户注册协议',index:'regisXy'},
                {ccon:'直播文明公约',index:'livePact'},
                {ccon:'帮助中心',index:'helpCen'}
            ],
            addre:[
                "联系地址: 深圳市南山区粤海街道科技园科苑路6号科技园工业大厦2F西侧226房",
                // "客服电话: 13238879702"
            ],
            gy:[],
      }
    },
    created(){
        let that = this
        this.http(this.configs.apiUl+'/user/app/aboutUs?'+this.configs.display,'get','',function(res){
            let datas=res.data
            if(datas.c == 0){
                that.gy = datas.d.about_us.slice(0,5)
            }
        })
    }
}



