import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import qs from 'qs'
export default {
    data () {
        return {
           userid: '',
           page: 1,
           pagesize: 8,
           items: [],
           pagetotal: 0,
           isphone: "",
           content: '正在加载...',
           isuser: false,
           anchor_is_live: 'N',
           user: {},
           user_id: '',
           anchor_live_pay: '',
           anchor_live_img: '',
           anchor_live_title: '',
           anchor_update_time: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleCurrentChange (val){//分页
            this.page = val
            let that = this
            if(this.isuser){
                this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+ val +"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.videos.items
                    }     
                })
            }else{
                this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&user_id="+this.userid+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.videos.items
                        that.pagetotal = datas.d.videos.total
                    }     
                })
            }
        },
        del (val) {//删除回放
            this.$confirm('删除后无法恢复', {
                    title: '确定删除吗？',
                    confirmButtonText: '确定删除',
                    cancelButtonText: '取消',
                    showClose: false,
                    center: true
                    }).then(() => {
                        let that = this
                        //删除回放
                        let data = qs.stringify({
                            "access_token": this.$store.state.access_token,
                            "display": "pc",
                            "log_id": val
                        })
                        this.http(this.configs.apiUl + "/live/playback/deletePlayBack","post",data ,function(res) {//获取回放列表
                            let datas = res.data
                            if(datas.c == 0){
                                that.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                                if(that.items.length <= 1){
                                    that.page = that.page-1
                                    if(that.page >=0){
                                        that.page = 1
                                    }
                                }
                                //获取主播回放
                                that.http(that.configs.apiUl + "/live/playback/anchor?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&page="+that.page+"&pagesize="+that.pagesize,"get","",function(res) {//获取回放列表
                                    var datas = res.data
                                    if(datas.c == 0){
                                        that.items = datas.d.videos.items
                                        that.pagetotal = datas.d.videos.total
                                        if(that.items.length == 0){
                                            that.content = '暂无数据'
                                        }
                                    }  
                                })
                            }else{
                                that.$message({
                                    type: 'error',
                                    message: datas.m
                                });
                            }
                        })
                    }).catch(() => {
                        //取消删除          
                });
        },
        go (id) {
            if(id == this.$store.state.user_id){
                // this.$router.push({path:'/anchorLive'})
                window.open('/anchorLive')
            }else{
                //  this.$router.push({path:'/liveRoom',query:{'id': id}})
                 window.open('/liveRoom?id='+id)
            }
        }
    },
    created () {
        this.userid = this.$route.query.uid//获取用户id
        if(this.userid != undefined){
            this.isuser = false
            let that = this
            //获取用户回放
            this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&user_id="+this.userid+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                var datas = res.data
                if(datas.c == 0){
                    that.anchor_is_live = datas.d.anchor_is_live
                    that.anchor_live_pay = datas.d.anchor_live_pay
                    that.anchor_live_img = datas.d.anchor_live_img
                    that.anchor_live_title = datas.d.anchor_live_title
                    that.anchor_update_time = datas.d.anchor_update_time
                    that.user_id = datas.d.user_id
                    console.log(datas.d)
                    that.items = datas.d.videos.items
                    that.pagetotal = datas.d.videos.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                    // for(var i =0; i<that.items.length;i++){
                    //     if(that.items[i].category_name[1] == '计时'){
                    //         console.log(that.items[i].category_name[1])
                    //         that.items.splice(i,1)
                    //     }
                    // }
                }     
            })
            //获取个人信息
            this.http(this.configs.apiUl + "/user/profile/card?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.userid,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                }
            })
        }else{
            this.isuser = true
            let that = this
            //获取主播回放
            this.http(this.configs.apiUl + "/live/playback/anchor?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取回放列表
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.videos.items
                    that.pagetotal = datas.d.videos.total
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }     
            })
            this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.user = datas.d                    
                }     
            })
        }
    }
}



