import $ from 'jquery'
export default {
  props: ['list'],
  data () {
      return {
          sj: true,
          type: '',
          imgs: "",
          id: '',
          player: {}
      }
  },
  methods: {
      pitch (id, k) {
        var that = this
        $('.vcp-player').remove()
        this.type = k
        //  this.imgs = img
        // this.player.destroy()//销毁播放器实例[v2.2.1+]
        setTimeout(()=>{
            if(this.list[k].anchor_live_log_playback_url){
                this.player = new TcPlayer('id_test_video', {
                    "m3u8": this.list[k].anchor_live_log_playback_url, //请替换成实际可用的播放地址
                    "flv": this.list[k].anchor_live_log_playback_url,
                    "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                    "coverpic" : this.list[k].anchor_live_img,
                    "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                    "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                    "wording": {
                        2032: "请求视频失败，请检查网络",
                        2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                        1002: '请求视频失败，请检查网络',
                        5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                    },
                    'listener': function(msg){
                        if(msg.type == "ended"){
                            $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                    }
                    // 'live': true,
                    // 'controls': 'none'
                }); 
            }else{
                this.player = new TcPlayer('id_test_video', {
                    "m3u8": this.list[k].anchor_live_play_m3u8, //请替换成实际可用的播放地址
                    "flv": this.list[k].anchor_live_play_flv,
                    "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                    "coverpic" : this.list[k].anchor_live_img,
                    "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                    "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                    "wording": {
                        2032: "请求视频失败，请检查网络",
                        2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                        1002: '请求视频失败，请检查网络',
                        5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                    },
                    'live': true,
                    'controls': 'none',
                    'listener': function(msg){
                       if(msg.type == "ended"){
                            $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                }
                }); 
            }
        })

      },
    go (k) {
        var id = this.list[k].user_id
        if(id == this.$store.state.user_id){
            // this.$router.push({path:'/anchorLive'})
            window.open('/anchorLive')
        }else{
            // this.$router.push({path:'/liveRoom',query:{'id': id}})
            window.open('/liveRoom?id='+id)
        }
    },
    play () {
        let that = this
        this.player.destroy()//销毁直播
        var k = this.type
        if(this.list[k].anchor_live_log_playback_url){
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[k].anchor_live_log_playback_url, //请替换成实际可用的播放地址
                "flv": this.list[k].anchor_live_log_playback_url,
                "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                "coverpic" : this.list[k].anchor_live_img,
                "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                "wording": {
                    2032: "请求视频失败，请检查网络",
                    2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                    1002: '请求视频失败，请检查网络',
                    5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                },
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                         setTimeout(()=>{
                             that.play()
                         },2000) 
                    }
                }
                // 'live': true,
                // 'controls': 'none'
            }); 
        }else{
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[k].anchor_live_play_m3u8, //请替换成实际可用的播放地址
                "flv": this.list[k].anchor_live_play_flv,
                "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                "coverpic" : this.list[k].anchor_live_img,
                "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                "wording": {
                    2032: "请求视频失败，请检查网络",
                    2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                    1002: '请求视频失败，请检查网络',
                    5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                },
                'live': true,
                'controls': 'none',
                'listener': function(msg){
                     if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                         setTimeout(()=>{
                             that.play()
                         },2000) 
                    }
                }
            }); 
        }
    }
  },
  watch: {
      list () {
        let that = this
        for(var i=0;i<this.list.length;i++){
            if(this.list[i].anchor_live_img == ''){
                this.list[i].anchor_live_img = '/static/images/lebobg.png'
            }
        }
        this.type = 0
        if(this.list[0].anchor_live_log_playback_url){
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[0].anchor_live_log_playback_url, //请替换成实际可用的播放地址
                "flv": this.list[0].anchor_live_log_playback_url,
                "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                "coverpic" : this.list[0].anchor_live_img,
                "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                "wording": {
                    1002: '即将直播，请稍等',
                    2032: "请求视频失败，请检查网络",
                    2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                    1002: '请求视频失败，请检查网络',
                    5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                },
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                         setTimeout(()=>{
                             this.player.load();//进行重连
                         },2000) 
                    }
                }
                // 'live': true,
                // 'controls': 'none'
            }); 
        }else{
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[0].anchor_live_play_m3u8, //请替换成实际可用的播放地址
                "flv": this.list[0].anchor_live_play_flv,
                "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                "coverpic" : this.list[0].anchor_live_img,
                "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                "wording": {
                    1002: '即将直播，请稍等',
                    2032: "请求视频失败，请检查网络",
                    2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                    1002: '请求视频失败，请检查网络',
                    5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                },
                'live': true,
                'controls': 'none',
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                         setTimeout(()=>{
                             that.play()
                            // that.player.load();//进行重连
                         },2000) 
                    }
                }
            }); 
        }
        // this.imgs = this.list[0].anchor_live_img
      }
  },
  created () {
        var that = this
        //判断浏览器是否在查看状态
        var hiddenProperty = 'hidden' in document ? 'hidden' :    
            'webkitHidden' in document ? 'webkitHidden' :    
            'mozHidden' in document ? 'mozHidden' :    
            null;
        var visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
        var onVisibilityChange = ()=>{
            if (!document[hiddenProperty]) {    
                // document.title='被发现啦(*´∇｀*)';
                // that.player.play()//播放直播
                var k = this.type
                if(this.list[k].anchor_live_log_playback_url){
                    this.player = new TcPlayer('id_test_video', {
                        "m3u8": this.list[k].anchor_live_log_playback_url, //请替换成实际可用的播放地址
                        "flv": this.list[k].anchor_live_log_playback_url,
                        "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                        "coverpic" : this.list[k].anchor_live_img,
                        "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                        "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                        "wording": {
                            2032: "请求视频失败，请检查网络",
                            2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                            1002: '请求视频失败，请检查网络',
                            5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                        },
                        'listener': function(msg){
                            if(msg.type == "ended"){
                                $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                                setTimeout(()=>{
                                    that.play()
                                },2000) 
                            }
                        }
                        // 'live': true,
                        // 'controls': 'none'
                    }); 
                }else{
                    this.player = new TcPlayer('id_test_video', {
                        "m3u8": this.list[k].anchor_live_play_m3u8, //请替换成实际可用的播放地址
                        "flv": this.list[k].anchor_live_play_flv,
                        "autoplay" : true,      //iOS下safari浏览器，以及大部分移动端浏览器是不开放视频自动播放这个能力的
                        "coverpic" : this.list[k].anchor_live_img,
                        "width" :  '100%',//视频的显示宽度，请尽量使用视频分辨率宽度
                        "height" : '100%',//视频的显示高度，请尽量使用视频分辨率高度
                        "wording": {
                            2032: "请求视频失败，请检查网络",
                            2048: "请求m3u8文件失败，可能是网络错误或者跨域问题",
                            1002: '请求视频失败，请检查网络',
                            5: '当前浏览器不支持 MSE 或者 Flash 插件未启用'
                        },
                        'live': true,
                        'controls': 'none',
                        'listener': function(msg){
                            if(msg.type == "ended"){
                                $('#id_test_video .vcp-error-tips').text('主播网络不佳，请稍后...').show()
                                setTimeout(()=>{
                                    that.play()
                                },2000) 
                            }
                        }
                    }); 
                }
            }else{
                // document.title='藏好啦(つд⊂)  ';
                // that.player.pause()//停止直播
                that.player.destroy()//销毁直播
                
            }
        }
        document.addEventListener(visibilityChangeEvent, onVisibilityChange);
  }
} 



