import vHeader from './public/header'
import vFooter from './public/footer'
import { Message } from 'element-ui'
export default {
    data () {
        return {
            je:'',
            account:'',
            tou:true,
            appear:true,
            erb:false,
            user: {},
            num: 60,
            iscode: true,
            Interval: '',
            phone: '',
            codes: '',
            withdraw: {"info": [
            {
                "cash": "1234.00元",
                "remark": "支付宝账号(18122711280)",
                "title": "提现申请已提交，等待系统审核"
            }
        ],
        "status": "C"
    },
            myDate: ''
        }
    },
    components: {
         vHeader,
         vFooter
    },
    methods: {
        next () {//下一步
        var phone = /^((13[0-9])|(15[1-3,5-9])|(17[7])|(18[0-9]))\d{8}$/;
        var email = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;  
            if(this.user.cash <= 0){
                Message({
                    message: '暂无可提现金额',
                    type: "error"
                })
                return false
            }else if(this.je == "" || isNaN(this.je)){
                Message({
                    message: '请输入提现金额',
                    type: "error"
                })
                return false
            }else if(this.je <= 0){
                Message({
                    message: '提现金额不能小于0',
                    type: "error"
                })
                return false
            }else if(this.account == ""){
                Message({
                    message: '请输入提现账号',
                    type: "error"
                })
                return false
            }else if(phone.test(this.account) == false && email.test(this.account) == false){
                Message({
                    message: '请手机号或者邮箱',
                    type: "error"
                })
                return false
            }else if(Number(this.je) > Number(this.user.cash)){
                Message({
                    message: '输入金额大于可提现金额',
                    type: "error"
                })
                return false
            }
            this.appear = false
            this.erb = true
        },
        back () {//返回上一步
            this.appear = true
            this.erb = false
        },
        confirm () {//确认提现
            if(this.codes == ""){
                Message({
                    message: '验证码不能为空',
                    type: "error"
                })
                return false
            }
            let that = this
            this.http(this.configs.apiUl + "/user/withdraw/add?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&account="+this.account+"&cash="+this.je+"&pay="+this.user.pay+"&code="+this.codes+"&withdraw_id="+this.user.withdraw_id,"get","",function(res) {
                 let datas = res.data
                 if(datas.c == 0){
                        Message({
                            message: '提现申请提交成功!',
                            type: 'success'
                        })
                        let withdraw_log_id = datas.d.withdraw_log.id
                        clearInterval(that.Interval)
                        setTimeout(function() {
                            that.appear = false
                            that.tou = false
                            that.erb = false
                        }, 1000);
                        that.http(that.configs.apiUl + "/user/withdraw/detail?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&withdraw_log_id="+withdraw_log_id,"get","",function(res) {
                            let datas = res.data
                            if(datas.c == 0){
                                that.withdraw = datas.d
                            }
                         })

                 }else{
                     Message({
                        message: datas.m,
                        type: 'error'
                    })
                 }
             })            
        },
        goback () {//返回我的账号
            window.history.back(); 
        },
        code () {//获取验证码
            let that = this
            if(this.iscode){
                this.http(this.configs.apiUl + "/account/verifycode/withdraw?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&account="+this.account+"&cash="+this.je+"&pay="+this.user.pay,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.iscode = false
                        Message({
                            message: '发送成功!',
                            type: 'success'
                        })
                        that.Interval = setInterval(function(){
                            that.num -= 1
                            if(that.num <= 0){
                                Message({
                                    message: '未收到验证码？点击重新发送'
                                })
                                that.num = 60
                                that.iscode = true
                                clearInterval(that.Interval)
                            }
                        },1000)
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                })
            }
        }
    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/user/withdraw/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {//获取提现信息
            let datas = res.data
            if(datas.c == 0){
                that.user = datas.d.user
            }
        })
        this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {//获取用户信息
            var datas = res.data
            if(datas.c == 0){
                that.phone = datas.d.user_phone                 
            }     
        })
    }
}



