import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
export default {
    data () {
        return {
           page: 1,
           pagesize: 10,
           items: [],
           pagetotal: 0,
           date_type: 'all',
           content: '正在加载...',
           istxLayer: false,
           date: {},
           time: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleCurrentChange (val){//分页
            this.page = val
            let that = this
            this.http(this.configs.apiUl + "/user/withdraw/log?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.withdraw_log.items
                }  
            })
        },
        hide () {
            this.istxLayer = false
        },
        dateils (id, time){//显示详情
            let that = this
            this.time = time
            this.http(that.configs.apiUl + "/user/withdraw/detail?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&withdraw_log_id="+id,"get","",function(res) {
                    let datas = res.data
                    if(datas.c == 0){
                        that.date = datas.d
                        that.istxLayer = true
                    }
            })
        }
    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/user/withdraw/log?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
            var datas = res.data
            if(datas.c == 0){
                that.items = datas.d.withdraw_log.items
                that.pagetotal = datas.d.withdraw_log.total
                if(that.items.length == 0){
                    that.content = '暂无数据'
                }
            }  
        })
    }
}



