import vHeader from './public/header'
import vFooter from './public/footer'
import $ from 'jquery'
export default {
    data () {
        return {
            recharge:[],
            user:{},
            type:'',
            paytype:1,
            money: '' 
        }
    },
    components: {
         vHeader,
         vFooter
    },
     methods: {
        combo_money(id, money){
            this.type = id
            this.money = money
        },
        payType(obj){
            if(obj == 1){
                this.paytype = 1;
                $(".zzfb").addClass("xz");
                $(".wwx").removeClass("xz");
            }else if(obj == 2){
                this.paytype = 2;
                $(".wwx").addClass("xz");
                $(".zzfb").removeClass("xz");
            }
        },
        renew () {//续费
            let that = this
            if(this.paytype == 1){//支付宝
                location.href = this.configs.apiUl+"/user/recharge/getPage?access_token="+this.$store.state.access_token+'&'+this.configs.display+"&recharge_combo_id="+this.type

            }else if(this.paytype == 2){//微信
                this.http(this.configs.apiUl+"/user/recharge/wx?access_token="+this.$store.state.access_token+'&'+this.configs.display+"&recharge_combo_id="+this.type+'&trade_type=NATIVE','get','',function(res){
                   let datas = res.data 
                   //var url = 'weixin://wxpay/bizpayurl?sign='+datas.d.sign+'&appid='+datas.d.appid+'&mch_id='+datas.d.partnerid+'&product_id='+datas.d.prepayid+'&time_stamp='+datas.d.timestamp+'&nonce_str='+datas.d.noncestr  
                    if(datas.c == 0){
                        //跳转支付页面
                        that.$router.push({path:"/payWay",query:{"name":"/recharge",'url':datas.d.code_url,'money': that.money}})
                    }
                })
            }
        }
    },
    created(){
        let that = this
        //获取列表信息
        this.http(this.configs.apiUl+"/user/recharge/index?access_token="+this.$store.state.access_token+'&'+this.configs.display,'get','',function(res){
            let datas=res.data
            if(datas.c == 0){
                that.recharge = datas.d.recharge_combo
                that.user = datas.d.user
                that.type = that.recharge[0].recharge_combo_id
                that.money = that.recharge[0].recharge_combo_fee
                that.user.user_coin = Number(that.user.user_coin).toFixed(2)
            }
        })                    
    }
}



