
import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import Clocker from 'vux/src/components/clocker/index.vue'

export default {
data () {
return {
activeName: 'first',
page: 1,
pagesize: 10,
deadline: '',
items:[],
pagetotal: 0,
content: '正在加载...'
}
},
methods: {
handleClick(tab, event) {
this.items = []
this.content = '正在加载...'
let that = this
this.page = 1
if(this.activeName == 'first'){

this.http(this.configs.apiUl+"/live/ranking/anchor?access_token="+this.$store.state.access_token+"&page="+this.page+"&pagesize="+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
let datas = res.data
if(datas.c == 0){
that.deadline = datas.d.deadline
that.items = datas.d.rank.items
that.pagetotal = datas.d.rank.total
if(that.items.length == 0){
that.content = '暂无数据'
}
}
})
}else if(this.activeName == 'second'){

this.http(this.configs.apiUl+"/live/ranking/user?access_token="+this.$store.state.access_token+"&page="+this.page+"&pagesize="+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
let datas=res.data
if(datas.c == 0){
that.deadlineu = datas.d.deadline
that.items = datas.d.rank.items
that.pagetotal = datas.d.rank.total
if(that.items.length == 0){
that.content = '暂无数据'
}
}
})
}
},
handleCurrentChange (val) {
let that = this
this.page = val
if(this.activeName == 'first'){

this.http(this.configs.apiUl+"/live/ranking/anchor?access_token="+this.$store.state.access_token+"&page="+this.page+"&pagesize="+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
let datas = res.data
if(datas.c == 0){
that.deadline = datas.d.deadline
that.items = datas.d.rank.items
}
})
}else if(this.activeName == 'second'){

this.http(this.configs.apiUl+"/live/ranking/user?access_token="+this.$store.state.access_token+"&page="+this.page+"&pagesize="+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
let datau=res.data
if(datau.c == 0){
that.deadlineu = datau.d.deadline
that.items = datau.d.rank.items
}
})
}
},
time (val){
function zeroize(num ,i) {
if(i == 1){
num = Number(num)+1
}
return (String(num).length == 1 ? '0' : '') + num;
}
var tmDate = new Date( val * 1000 );  
var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();
return  Y + '-'+ zeroize(m) + '-' + zeroize(d,1)
}
},
components: {
vHeader,
userBanner,
Formation,
vFooter,
Clocker
},
created(){
let that = this

this.http(this.configs.apiUl+"/live/ranking/anchor?access_token="+this.$store.state.access_token+"&page="+this.page+"&pagesize="+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
let datas = res.data
if(datas.c == 0){
that.deadline = datas.d.deadline
that.items = datas.d.rank.items
that.pagetotal = datas.d.rank.total
if(that.items.length == 0){
that.content = '暂无数据'
}
}
})

}
}



