import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import search from '@/components/search'
import liveRoom from '@/components/liveRoom'
import live from '@/components/live'
import bang from '@/components/my/bang'
import earn from '@/components/my/earn'
import manageroom from '@/components/my/manageroom'
import member from '@/components/my/member'
import privateMsg from '@/components/my/privateMsg'
import realName from '@/components/my/realName'
import recharge from '@/components/my/recharge'
import set from '@/components/my/set'
import userHome from '@/components/my/userHome'
import withdraw from '@/components/my/withdraw'
import spend from '@/components/my/spend'
import register from '@/components/user/register'
import seekPwd from '@/components/user/seekPwd'
import apply from '@/components/apply'
import notReal from '@/components/notReal'
import pay from '@/components/pay'
import payWay from '@/components/payWay'
import bindTel from '@/components/my/bindTel'
import userLive from '@/components/userLive'
import playBack from '@/components/playBack'
import gameIntro from '@/components/gameIntro'
import regisXy from '@/components/regisXy'
import help from '@/components/help'
import helpCen from '@/components/helpCen'
import livePact from '@/components/livePact'
import liveLayer from '@/components/liveLayer'
import anchorLive from '@/components/anchorLive'
import userPlayback from '@/components/my/userPlayback'
import privacyPolicy from '@/components/privacyPolicy'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        requireAuth: false, 
        title: '雨音首页-视频直播社交平台'
      }
    },
    {
      path: '/apply',
      name: 'apply',
      component: apply,
      meta: {
        requireAuth: true, 
        title: '提现申请'
      }
    },
    {
      path: '/notReal',
      name: 'notReal',
      component: notReal,
      meta: {
        requireAuth: false, 
        title: '未实名认证'
      }
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay,
      meta: {
        requireAuth: true,
        title: '充值'
      }
    },
    {
      path: '/payWay',
      name: 'payWay',
      component: payWay,
      meta: {
        requireAuth: false, 
        title: '扫码支付'
      }
    },
    {
      path: '/register',
      name: 'register',
      component: register,
      meta: {
        requireAuth: false,
        title: '登录'
      }
    },
    {
      path: '/seekPwd',
      name: 'seekPwd',
      component: seekPwd,
      meta: {
        requireAuth: false, 
        title: '找回密码'
      }
    },
    {
      path: '/search',
      name: 'search',
      component: search,
      meta: {
        requireAuth: false, 
        title: '搜索'
      }
    },
    {
      path: '/liveRoom',
      name: 'liveRoom',
      component: liveRoom,
      meta: {
        requireAuth: false, 
        title: '直播间'
      }
    },
    {
      path: '/live',
      name: 'live',
      component: live,
      meta: {
        requireAuth: false, 
        title: '分类'
      }
    },
    {
      path: '/bang',
      name: 'bang',
      component: bang,
      meta: {
        requireAuth: true, 
        title: '排行榜'
      }
    },
    {
      path: '/earn',
      name: 'earn',
      component: earn,
      meta: {
        requireAuth: true, 
        title: '我的收益'
      }
    },
    {
      path: '/manageroom',
      name: 'manageroom',
      component: manageroom,
      meta: {
        requireAuth: true, 
        title: '房管列表'
      }
    },
    {
      path: '/member',
      name: 'member',
      component: member,
      meta: {
        requireAuth: true, 
        title: '我的会员'
      }
    },
    {
      path: '/privateMsg',
      name: 'privateMsg',
      component: privateMsg,
      meta: {
        requireAuth: true, 
        title: '我的消息'
      }
    },
    {
      path: '/realName',
      name: 'realName',
      component: realName,
      meta: {
        requireAuth: true,
        title: '实名认证'
      }
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge,
      meta: {
        requireAuth: true, 
        title: '充值记录'
      }
    },
    {
      path: '/set',
      name: 'set',
      component: set,
      meta: {
        requireAuth: true, 
        title: '设置'
      }
    },
    {
      path: '/userHome',
      name: 'userHome',
      component: userHome,
      meta: {
        requireAuth: true, 
        title: '直播回放'
      }
    },
    {
      path: '/withdraw',
      name: 'withdraw',
      component: withdraw,
      meta: {
        requireAuth: true, 
        title: '我的提现'
      }
    },
    {
      path: '/spend',
      name: 'spend',
      component: spend,
      meta: {
        requireAuth: true, 
        title: '我的消费'
      }
    },
    {
      path: '/bindTel',
      name: 'bindTel',
      component: bindTel,
      meta: {
        requireAuth: true, 
        title: '绑定手机'
      }
    },
    {
      path: '/userLive',
      name: 'userLive',
      component: userLive,
      meta: {
        requireAuth: false, 
        title: '用户直播间'
      }
    },
    {
      path: '/playBack',
      name: 'playBack',
      component: playBack,
      meta: {
        requireAuth: true, 
        title: '查看回放'
      }
    },
    {
      path: '/gameIntro',
      name: 'gameIntro',
      component: gameIntro,
      meta: {
        requireAuth: false, 
        title: '游戏介绍'
      }
    },
    {
      path: '/regisXy',
      name: 'regisXy',
      component: regisXy,
      meta: {
        requireAuth: false, 
        title: '用户注册协议'
      }
    },
    {
      path: '/help',
      name: 'help',
      component: help,
      meta: {
        requireAuth: false, 
        title: '帮助'
      }
    },
    {
      path: '/helpCen',
      name: 'helpCen',
      component: helpCen,
      meta: {
        requireAuth: false, 
        title: '帮助中心'
      }
    },
    {
      path: '/livePact',
      name:'livePact',
      component: livePact,
      meta: {
        requireAuth: false, 
        title: '直播公约'
      }
    },
    {
      path: '/liveLayer',
      name: 'liveLayer',
      component: liveLayer,
      meta: {
        requireAuth: false,
        title: '直播弹框'
      }
    },
    {
      path: '/anchorLive',
      name: 'anchorLive',
      component: anchorLive,
      meta: {
        requireAuth: true,
        title: '直播间'
      }
    },
    {
      path: '/userPlayback',
      name: 'userPlayback',
      component: userPlayback,
      meta: {
        requireAuth: false,
        title: '主页'
      }
    },
    {
      path: '/privacyPolicy',
      name: 'privacyPolicy',
      component: privacyPolicy,
      meta: {
        requireAuth: false,
        title: '隐私协议'
      }
    }
    
  ]
})



