import $ from 'jquery'
import qs from 'qs'
import { Message } from 'element-ui'
export default {
    props: ['userid'],
    data () {
        return {
            isuser: true,
            user: {},
            isphone: '',
            imageUrl: '',
            original: true,
            afterwards: false,
            laterName: '',
            uploadUrl: this.configs.uploadUrl
        }
    },
    methods: {
        //编辑姓名
        changeName(){
            this.afterwards = true
            this.original = false  
            setTimeout(()=>{
                $('.user_names input.box').focus()
            })
           
        },
        //失去焦点
        unfocused(){
            let that = this
            let data = qs.stringify({
                'access_token': this.$store.state.access_token,
                'user_nickname': this.laterName,
                'display': 'pc'
            })
            if(this.laterName == ''){
                this.afterwards = false
                this.original = true
            }else{
                this.$confirm('您修改了资料确定要保存吗？',{
                    title: '提示',
                    confirmButtonText:'确定保存',
                    cancelButtonText: '取消',
                    showClose: false,
                    center: true
                }).then(() =>{
                    this.http(this.configs.apiUl+"/user/profile/update",'post',data,function(res){
                        let datas = res.data
                        if(datas.c == 0){  
                            window.location.reload()   
                        }else{
                            that.$message({
                                message: datas.m
                            });
                        }
                    })
                }).catch( () => {
                    //取消保存
                    this.original = true
                    this.afterwards = false
                    this.laterName = ''
                })
            }
        },
        //按Enter键
        car(){
            if(this.laterName == ''){
                Message({
                    message: '不能为空',
                    type: 'error'
                })
                return false
            }
            let that = this
            let data = qs.stringify({
                'access_token': this.$store.state.access_token,
                'user_nickname': this.laterName,
                'display': 'pc'
            })
            // this.$confirm('',{
            //     title: '确定保存吗?',
            //     confirmButtonText: '确定保存',
            //     cancelButtonText: '取消',
            //     showClose: false,
            //     center: true
            // }).then(() => {
            this.http(this.configs.apiUl+'/user/profile/update','post',data,function(res){
                let datas = res.data
                if(datas.c == 0){
                    window.location.reload()
                }else{
                    that.$message({
                        message: datas.m
                    });
                }
            })
            // }).catch( () => {
            //     //取消保存
            //     this.original = true
            //     this.afterwards = false
            //     this.laterName = ''
            // })

        },
        //重新上传封面
        handleAvatarSuccess(response,file,fileList){
            this.imageUrl = response.d.url
            //修改资料
            let data = qs.stringify({
                'access_token': this.$store.state.access_token,
                'user_avatar': this.imageUrl,
                'display': 'pc'
            })
            this.http(this.configs.apiUl+"/user/profile/update",'post',data,function(res){
                let datas = res.data
                if(datas.c == 0){
                    window.location.reload()
                }
            })
        },
        beforeAvatarUpload(){

        },
        attention (id) {//关注
            let that = this
            this.http(this.configs.apiUl + "/user/follow/add?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+id,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                   
                    that.user.is_follow = 'Y'
                    that.$message({
                        type: 'success',
                        message: '关注成功!'
                    });
                }else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    });
                }
            })
        },
        unattention (id) {//取消关注
            let that = this
            this.http(this.configs.apiUl + "/user/follow/delete?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+id,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                    that.user.is_follow = 'N'
                    that.$message({
                        type: 'success',
                        message: '取消成功!'
                    });
                }else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    });
                }
            })
        },
        tx () {//提现
            if(this.isphone == ""){
                this.$confirm('您还未绑定手机号，是否现在去绑定', {
                    title: '提示',
                    confirmButtonText: '是',
                    cancelButtonText: '取消',
                    showClose: false,
                    center: true
                    }).then(() => {
                        this.$router.push({path:"/bindTel?state=unverified"})
                    }).catch(() => {
                        //取消删除          
                });
            }else{
                this.$router.push({path:"/apply"})
            }
        },
        userProfile () {//获取个人信息
            let that = this
            this.http(this.configs.apiUl + "/user/profile/card?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.userid,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                    if(that.user.user_sex == '1'){
                        that.user.user_sex = '男'
                    }else{
                        that.user.user_sex = '女'
                    }
                    if(Number(that.user.user_follow_total) >= 10000){
                        that.user.user_follow_total = (Number(that.user.user_follow_total) /10000).toFixed(1) +'万'
                    }
                    if(Number(that.user.user_fans_total) >= 10000){
                        that.user.user_fans_total = (Number(that.user.user_fans_total) /10000).toFixed(1) +'万'
                    }
                }
            })
        }
    },
    created () {
        let that = this
         //判断是否用户
        if(that.userid != undefined){
            that.userProfile()
            that.isuser = false
            return false
        }
        //获取个人信息
        this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.user = datas.d
                that.isphone = datas.d.user_phone
                //判断是否用户
                // if(that.userid != undefined){
                //     if(that.userid == that.user.user_id){
                //         that.isuser = true
                //     }else{
                //         that.userProfile()
                //         that.isuser = false
                //     }
                // }else{
                //     that.isuser = true
                // }
            }
        })
    }
}



