import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
export default {
    data () {
        return {
           user: {},
           check: {}
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {

    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.user = datas.d
            }
        })
        this.http(this.configs.apiUl + "/user/certification/check?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            let datas = res.data
             if(datas.c == 0){
                 that.check = datas.d
             }

        })
    }
}



