export default {
    props: ['index'],
    data () {
        return {
            input:'',
            searchs:'',
            deng:true,
            intro: false,
            already:false,
            user: {}
        }
    },
    watch: {
        dialogTableVisible () {
            this.$store.commit('hidwRegister')//隐藏登录框
            this.dialogTableVisible = true
        }
    },
    computed: {
        loginstate () {
            let that = this
            if(this.$store.state.access_token == '' || this.$store.state.access_token == "undefined"){
               return true
            }else{
                 this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                   var datas = res.data
                    if(datas.c == 0){
                        that.user = datas.d
                        that.$store.state.user_id = datas.d.user_id//储存主播id
                        that.$store.state.ws_url = datas.d.ws_url//储存主播socket地址
                        that.$store.commit('hidwRegister')//隐藏登录框
                         return false
                    }else{
                        localStorage.setItem('access_token','')//localStorage清除用户登录信息
                        that.$store.state.access_token = ""//vuex清除用户登录信息
                        // that.$store.commit('showRegister')//显示登录框
                        return true
                    }
                })
            }
        }
    },
    methods: {
        shift (num) {
            if(Number(num) >= 10000){                
                return (Number(num) /10000).toFixed(2) +'万'
            }
            return num
        },
       search () {
           this.$router.push({"path":"/search",'query':{"search":this.searchs}})
           this.$emit("searchParent",this.searchs)
       },
       login () {
           this.$store.commit('showRegister')//显示登录框
           this.$store.state.visitState = 'login'
       },
       register () {
           this.$store.commit('showRegister')//显示注册框
           this.$store.state.visitState = 'register'
       },
       usershow () {
           if(this.intro){
               this.intro = false
           }else{
               this.intro = true
           }
       },
       loginOut () {//退出登录
            let that = this
             this.http(this.configs.apiUl + "/pc/login/logout?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                 let datas = res.data
                 if(datas.c == 0){
                    //  that.$store.commit('showRegister')//显示登录框
                     localStorage.setItem('access_token','')//localStorage清除用户登录信息
                     that.$store.state.access_token = ""//vuex清除用户登录信息
                     that.$store.state.user_id = ''//清除主播id信息
                     that.intro = false
                     location.reload();//刷新页面
                 }
             })
       },
       myHome () {
           window.open('/userHome')
        //    this.$router.push({path:'/userHome'})
            //location.reload();
       }
    },
    mounted () {
        
    },
    created () {
       this.searchs = this.$route.query.search
    }
}



