import vHeader from './public/header'
import vFooter from './public/footer'
export default {
    data () {
        return {
             activeName: 'first',
             search: '',
             lsititems:[],
             pagetotal: 0,
             pagesize: 20,
             page:1,
             content: '正在加载...'
        }
    },
    components: {
        vHeader,
        vFooter
    },
    methods: {
        go (id) {
            // location.reload();//刷新页面
            if(id == this.$store.state.user_id){
                // this.$router.push({path:'/anchorLive'})
                window.open('/anchorLive')
            }else{
                // this.$router.push({path:'/liveRoom',query:{'id': id}})
                window.open('/liveRoom?id='+id)
            }
        },
         handleClick(tab, event) {
            if(this.$store.state.user_id == '' && this.activeName == 'second'){
                this.$confirm('您未登陆，是否马上登陆', {
                    title: '提示',
                    confirmButtonText: '是',
                    cancelButtonText: '取消',
                    showClose: false,
                    center: true
                    }).then(() => {
                        this.$store.commit('showRegister')
                        // this.$router.push({path:"/bindTel?state=unverified"})
                    }).catch(() => {
                        this.activeName = 'first'
                        this.searchList()
                        //取消删除          
                });
            }
             this.searchList()
         },
         handleCurrentChange (val) {
             this.page = val
             let that = this
             if(this.activeName == 'first'){
                    this.http(this.configs.apiUl+"/pc/app/searchLive?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&key="+this.search+"&page="+this.page+"&pagesize="+this.pagesize,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.lsititems = datas.d.items
                            that.pagetotal = datas.d.total
                        }
                    })
                }else if(this.activeName == 'second'){
                    this.http(this.configs.apiUl+"/pc/app/searchUser?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&key="+this.search+"&page="+this.page+"&pagesize="+this.pagesize,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.lsititems = datas.d.items
                            that.pagetotal = datas.d.total
                            if(Number(that.lsititems.user_follow_total) >= 10000){
                                that.lsititems.user_follow_total = (Number(that.lsititems.user_follow_total) /10000).toFixed(2) +'万'
                            }
                            if(Number(that.lsititems.user_fans_total) >= 10000){
                                that.lsititems.user_fans_total = (Number(that.lsititems.user_fans_total) /10000).toFixed(2) +'万'
                            }
                        }
                    })
                }
         },
         Fsearch (val) {
            this.search = val
            this.searchList()
         },
         searchList () {
                this.lsititems = []
                let that = this
                this.page = 1
                that.content = '正在加载...'
                if(this.activeName == 'first'){
                    this.http(this.configs.apiUl+"/pc/app/searchLive?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&key="+this.search+"&page="+this.page+"&pagesize="+this.pagesize,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.lsititems = datas.d.items
                            that.pagetotal = datas.d.total
                            if(that.lsititems.length == 0){
                                that.content = '暂无数据'
                            }
                        }
                    })
                }else if(this.activeName == 'second'){

                    console.log(this.$store.state.user_id)
                    this.http(this.configs.apiUl+"/pc/app/searchUser?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&key="+this.search+"&page="+this.page+"&pagesize="+this.pagesize,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.lsititems = datas.d.items
                            that.pagetotal = datas.d.total
                            if(Number(that.lsititems.user_follow_total) >= 10000){
                                that.lsititems.user_follow_total = (Number(that.lsititems.user_follow_total) /10000).toFixed(2) +'万'
                            }
                            if(Number(that.lsititems.user_fans_total) >= 10000){
                                that.lsititems.user_fans_total = (Number(that.lsititems.user_fans_total) /10000).toFixed(2) +'万'
                            }
                            if(that.lsititems.length == 0){
                                that.content = '暂无数据'
                            }
                        }
                    })
                }
         }
    },
    created () {
        this.search = this.$route.query.search
        let that = this
        this.http(this.configs.apiUl+"/pc/app/searchLive?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&key="+this.search+"&page="+this.page+"&pagesize="+this.pagesize,'get','',function(res){
            var datas = res.data
            if(datas.c == 0){
                that.lsititems = datas.d.items
                that.pagetotal = datas.d.total 
                if(that.lsititems.length == 0){
                    that.content = '暂无数据'
                }
            }
        })
    }
}



