import qs from 'qs'
import { Message } from 'element-ui'
import $ from 'jquery'
export default {
    name: 'register',
    data () {
        return {
            dialogTableVisible:true,
            activeName: 'first',
            user:'',
            pwd:'',
            veri:'',
            phone:'',
            checked: false,
            num: 60,
            iscode: true,
            Interval: '',
            password: '',
            invite_code: '',
            redirect: ''
        }
    },
    watch: {
        dialogTableVisible () {
            // console.log(this.$route.path)
            if(this.$route.path == '/register'){
                this.$router.push({path: '/' })
            }else{
                this.$store.commit('hidwRegister')//隐藏登录框
                this.dialogTableVisible = true
            }
            
        }
    },
    methods: {
        handleClick(tab, event) {
            if(this.activeName == 'first'){
                setTimeout(function(){
                    $('#user').focus()
                })
            }else if(this.activeName == 'second'){
                setTimeout(function(){
                    $("#register").focus()
                })
            }
            
        },
        seek () {
            this.$store.commit('showisseekPwd')//显示修改密码
        },
        code () {//获取验证码
            let that = this
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空！',
                    type: "error"
                })
                return false
            }
            if(this.iscode){
                this.http(this.configs.apiUl + "/pc/verifycode/register?phone="+this.phone, "get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.iscode = false
                        Message({
                        message: '发送成功!',
                        type: 'success'
                        })
                        that.Interval = setInterval(function(){
                            that.num -= 1
                            if(that.num <= 0){
                                Message({
                                    message: '未收到验证码？点击重新发送'
                                })
                                that.num = 60
                                that.iscode = true
                                clearInterval(that.Interval)
                            }
                        },1000)
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                })
            }
        },
        register () {//提交注册
            let that = this
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空！',
                    type: "error"
                })
                return false
            }else if(this.veri == ""){
                Message({
                    message: '验证码不能为空！',
                    type: "error"
                })
                return false
            }else if(this.password == ""){
                Message({
                    message: '密码不能为空！',
                    type: "error"
                })
                return false
            } else if (this.password.length < 6 || this.password.length > 20) {
                Message({
                        message: '密码长度为6-20位',
                        type: "error"
                    })
                return false;
            }else if(this.checked == false){
                Message({
                    message: '请勾选用户注册协议',
                    type: "error"
                })
                return false;
            }
            let data = qs.stringify({
                "code": this.veri,
                "password": this.password,
                "phone": this.phone,
                "invite_code": this.invite_code
            })        
            this.http(this.configs.apiUl + "/pc/register/index", "post", data ,function(res) {
                var datas = res.data
                if(datas.c == 0){ 
                    Message({
                        message: '注册成功，立即登录',
                        type: 'success'
                    })
                    clearInterval(that.Interval)
                    that.activeName = 'first'
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        loginuser () {
            let that = this
            if(this.user == ''){
                this.user = $('#user').val()
            }
            if(this.pwd == ""){
                this.pwd = $('#pwd').val()
            }
            if(this.user == ""){
                Message({
                    message: '用户名不能为空!',
                    type: "error"
                })
                return false
            }else if(this.pwd == ""){
                Message({
                    message: '密码不能为空！',
                    type: "error"
                })
                return false
            }
            this.http(this.configs.apiUl + "/pc/login/index?password="+this.pwd+"&phone="+this.user, "get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    // Message({
                    //     message: '登录成功',
                    //     type: 'success'
                    // })
                    localStorage.setItem('access_token',datas.d.token)//localStorage储存用户登录信息
                    that.$store.state.access_token = datas.d.token//vuex储存用户登录信息
                    that.$store.state.user_id = datas.d.user_id//储存主播id
                    setTimeout(function(){
                        that.$store.commit('hidwRegister')//隐藏登录框
                    },2000)
                     setTimeout(function(){
                         location.reload();//刷新页面
                    })
                   
                    if(that.redirect != ''){
                        that.$router.push(that.redirect)
                    }
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })

        },
        weibo () {//微博登录
        /**
            if(WB2.checkLogin()){//检查是否已登录
                WB2.logout(function(){//退出登录方法
                    //回调方法
                });
            }
            WB2.login(function(){//登录授权
                WB2.anyWhere(function(W){
                    W.parseCMD('/account/get_uid.json',function(oResult1,bStatus){//获取用户uid
                    console.log(oResult1)
                        if(bStatus){
                            W.parseCMD('/users/show.json',function(oResult2,bStatus){//通过uid获取用户信息
                                if(bStatus){
                                    var args = {
                                        openid:oResult2.id,//获取用户openid
                                        access_token:WB2.oauthData.access_token,//获取用户access_token
                                        username:oResult2.name,//获取用户名
                                        userHeadImg:oResult2.profile_image_url,//获取用户微博头像
                                    }
                                    //然后根据实际情况进行自己网站的一些认证处理
                                    console.log(args)
                                }
                            },{uid:oResult1.uid},{method:'get',cache_time:30});
                        }
                    },{},{method:'get',cache_time:30});//默认是post请求方法
                });
            });
            */
            localStorage.setItem('access_token','otherLogin')//localStorage储存用户第三方登录
            let location_url = window.location.href
            let leng = location_url.indexOf("?") 
            if(location_url.slice(leng-9,leng) == '/register'){
                let url_leng = location_url.indexOf("/register")
                location_url = location_url.slice(0, url_leng)+this.$route.query.redirect 
            }
            window.location.href= this.configs.apiUl + "/pc/wboauth/web?location_url="+location_url

        },
        qq () {//qq登录
        /** 
             let oOpts = {
                    appId:"101446061",
                    redirectURI:"http%3a%2f%2fpc.lebotv.cc%2f%23%2f"
                }
                QC.Login.showPopup(oOpts)

                if(QC.Login.check()){//检查是否已登录
                    QC.Login.signOut();//退出登录
                }
                QC.Login({
                    btnId:"qqLoginBtn",//插入按钮的节点id，必选,可为空字符串
                    scope:"all",//用户需要确认的scope授权项，可选，默认all
                    size: "A_XL"//按钮尺寸，可用值[A_XL| A_L| A_M| A_S|  B_M| B_S| C_S]，可选，默认B_S
                },function(reqData,opts){
                    //登录成功回调方法
                    QC.Login.getMe(function(openId,accessToken){
                        var args = {
                            openid:openId,
                            userHeadImg:reqData.figureurl_qq_2,
                            access_token:accessToken,
                        };
                    });
                    QC.api('get_user_info',{}).success(function(userdata){
                        //可以获得用户的各种相关信息，如用户昵称
                        var username = userdata.data.nickname;
                    });
                },function(opts){
                    //注销成功回调方法
                });
            */
            localStorage.setItem('access_token','otherLogin')//localStorage储存用户第三方登录
            let location_url = window.location.href
            let leng = location_url.indexOf("?") 
            if(location_url.slice(leng-9,leng) == '/register'){
                let url_leng = location_url.indexOf("/register")
                location_url = location_url.slice(0, url_leng)+this.$route.query.redirect 
            }
            window.location.href= this.configs.apiUl + "/pc/qqoauth/web?location_url="+location_url
        },
        weix () {//微信登录
            /**
             * //ifame 方式显示二维码
             * var obj = new WxLogin({
                id:"login_container", 
                appid: "wx0800b96c0ef51eda", 
                scope: "snsapi_login", 
                redirect_uri: "http%3a%2f%2fpc.lebotv.cc%2f%23%2f",
                state: "STATE#wechat_redirect"
                });
            
                var path = 'http%3a%2f%2fpc.lebotv.cc%2f%23%2f';//登录后回调的地址
                var appid = 'wx0800b96c0ef51eda';//注册申请的appid
                window.open('https://open.weixin.qq.com/connect/qrconnect?appid='+appid+'&redirect_uri='+path+'&response_type=code&scope=snsapi_login&state=STATE#wechat_redirect');
            */
            localStorage.setItem('access_token','otherLogin')//localStorage储存用户第三方登录
            let location_url = window.location.href
            let leng = location_url.indexOf("?") 
            if(location_url.slice(leng-9,leng) == '/register'){
                let url_leng = location_url.indexOf("/register")
                location_url = location_url.slice(0, url_leng)+this.$route.query.redirect 
            }
            window.location.href= this.configs.apiUl + "/pc/wxoauth/web?location_url="+location_url

        }
    },
    mounted (){
        if(this.activeName == 'first'){
            setTimeout(function() {
                 $("#user").focus()
            });
        }else if(this.activeName == 'second'){
            setTimeout(function() {
                $("#register").focus()
            });
        }
    }, 
    created () {
        let that = this
        if(this.$store.state.visitState == 'register'){
            this.activeName = 'second'
        }
        this.redirect = this.$route.query.redirect 
    },
    
}



