import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
export default {
    data () {
        return {
            value1: '',
            page: 1,
            pagesize: 10,
            items: [],
            pagetotal: 0,
            date_type: 'all',
            content: '正在加载...',
            chart: false,
            text: '送礼'
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    watch: {
        value1 () {
        //   var date = this.value1;
        //   date = date.substring(0,19);    
        //   date = date.replace(/-/g,'/'); 
        //   var timestamp = new Date(date).getTime() /1000;//转换时间戳
            let that = this
            that.content = '正在加载...'
            this.page = 1
            if(this.text == '送礼'){
                this.http(this.configs.apiUl + "/user/amountrecord/giftDecreaseCoin?access_token="+this.$store.state.access_token+"&date="+this.value1+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.record_list.items
                        that.pagetotal = datas.d.record_list.total
                        if(that.items.length == 0){
                            that.content = '暂无数据'
                        }
                    }  
                })
            }else{
                this.http(this.configs.apiUl+"/user/amountrecord/vipDecreaseCoin?access_token="+this.$store.state.access_token+"&date="+this.value1+'&'+this.configs.display+'&page='+this.page+'&pagesize='+this.pagesize,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.record_list.items
                        that.pagetotal = datas.d.record_list.total
                        if(that.items.length == 0){
                            that.content = '暂无数据'
                        }
                    }  
                })
            }
        }
    },
    methods: {
        handleCurrentChange (val){//分页
            this.page = val
            let that = this
            //礼物
            if(this.text == '送礼'){
                if(this.value1 == ""){
                    this.http(this.configs.apiUl + "/user/amountrecord/giftDecreaseCoin?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {
                        var datas = res.data
                        if(datas.c == 0){
                            that.items = datas.d.record_list.items
                            that.pagetotal = datas.d.record_list.total
                        }  
                    })
                }else{
                    this.http(this.configs.apiUl + "/user/amountrecord/giftDecreaseCoin?access_token="+this.$store.state.access_token+"&date="+this.value1+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                        var datas = res.data
                        if(datas.c == 0){
                            that.items = datas.d.record_list.items
                            that.pagetotal = datas.d.record_list.total
                        }  
                    })
                }
            }else{
                //VIP
                if(this.value1 == ""){
                        this.http(this.configs.apiUl+"/user/amountrecord/vipDecreaseCoin?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&date_type='+this.date_type+'&page='+this.page+'&pagesize='+this.pagesize,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.items = datas.d.record_list.items
                            that.pagetotal = datas.d.record_list.total
                        }
                    })
                }else{
                    this.http(this.configs.apiUl+"/user/amountrecord/vipDecreaseCoin?access_token="+this.$store.state.access_token+"&data="+this.value1+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pageisze,'get','',function(res){
                        var datas = res.data
                        if(datas.c == 0){
                            that.items = datas.d.record_list.items
                            that.pagetotal = datas.d.record_list.total
                        }
                    })
                }
            }           
        },
        reveal(){
            if(this.chart){
                this.chart = false
            }else{
                this.chart = true
            }
        },
        present(){
            let that = this
            this.http(this.configs.apiUl + "/user/amountrecord/giftDecreaseCoin?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.record_list.items
                    that.pagetotal = datas.d.record_list.total
                    that.text = '送礼'
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }  
            })
            this.chart = false
        },
        vip(){
            let that = this
            this.http(this.configs.apiUl+"/user/amountrecord/vipDecreaseCoin?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&date_type='+this.date_type+'&page='+this.page+'&pagesize='+this.pagesize,'get','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.record_list.items
                    that.pagetotal = datas.d.record_list.total
                    that.text = 'VIP'
                    if(that.items.length == 0){
                        that.content = '暂无数据'
                    }
                }
            })
            this.chart = false
        }
    },
    created () {
        let that = this
        this.http(this.configs.apiUl + "/user/amountrecord/giftDecreaseCoin?access_token="+this.$store.state.access_token+"&date_type="+this.date_type+"&"+this.configs.display+"&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.items = datas.d.record_list.items
                that.pagetotal = datas.d.record_list.total
                if(that.items.length == 0){
                    that.content = '暂无数据'
                }
            }  
        })
        // this.http(this.configs.apiUl+"/user/amountrecord/vipDecreaseCoin?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&page='+this.page+'&pagesize='+this.pagesize,'get','',function(res){
        //     var datas = res.data
        //     if(datas.c == 0){
        //         that.items = datas.d.record_list.items
        //         that.pagetotal = datas.d.record_list.total
        //         if(that.items.length == 0){
        //             that.content = '暂无数据'
        //         }
        //     } 
        // })
    }
}



