import vHeader from './public/header'
import vFooter from './public/footer'
export default {
    data () {
        return {
            tabPosition: 'left',
            activeName: '',
            about:[],
        }
    },
    components: {
        vHeader,
        vFooter
    },
    computed: {
        active () {
            this.activeName = this.$route.query.name
        }
    },
    created(){
        let that = this
        this.http(this.configs.apiUl+'/user/app/aboutUs?'+this.configs.display,'get','',function(res){
            let datas = res.data
            if(datas.c == 0){
                 that.about = datas.d.about_us
            }
        })
    }
}



