import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isRegister : false,//控制登录框是否显示
        isseekPwd: false,//控制忘记密码是否显示
        visitState: '',//控制是否点击注册或登录
        access_token: '',//用户唯一表示
        user_id: '',//主播ID
        ws_url: '',//socket地址
        userSign: '',//游客模式IM
        isgift: false,//是否隐藏礼物框
    },
    mutations: {
        showRegister (state) {
            state.isRegister = true
            state.isseekPwd = false
        },
        showisseekPwd (state) {
            state.isRegister = false
            state.isseekPwd = true
        },
        hidwRegister (state) {
            state.isRegister = false
            state.isseekPwd = false
        }
    }
});


