import qs from 'qs'
import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import { Message } from 'element-ui'
export default {
    data () {
        return {
           name:'',
           iden:'',
           tel:'',
           imageUrl1: '',
           imageUrl2: '',
           imageUrl3: '',
           apply:true,
           user_certification_result: '',
           success:false,
           fail:false,
           pass:false,
           uploadUrl: this.configs.uploadUrl
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        handleAvatarSuccess1 (response, file, fileList) {
            this.imageUrl1 = response.d.url
        },
        handleAvatarSuccess2 (response, file, fileList) {
            this.imageUrl2 = response.d.url
        },
        handleAvatarSuccess3 (response, file, fileList) {
            this.imageUrl3 = response.d.url
        },
        instant () {
            let that = this
            if(this.name == ""){
                Message({
                    message: '请输入真实姓名',
                    type: "error"
                })
                return false
            }else if(this.iden == ''){
                Message({
                    message: '请输入证件号码',
                    type: "error"
                })
                return false
            }else if(this.tel == ''){
                Message({
                    message: '请输入手机号码',
                    type: "error"
                })
                return false
            }else if(this.imageUrl1 == ''){
                Message({
                    message: '请上传身份证正面照',
                    type: "error"
                })
                return false
            }else if(this.imageUrl2 == ''){
                Message({
                    message: '请上传身份证反面照',
                    type: "error"
                })
                return false
            }else if(this.imageUrl3 == ''){
                Message({
                    message: '请上传身份证手持照',
                    type: "error"
                })
                return false
            }
            let data = qs.stringify({
                "number": this.iden,
                "realname": this.name,
                "phone": this.tel,
                "back_img": this.imageUrl2,
                "front_img": this.imageUrl1,
                "user_img": this.imageUrl3,
                "access_token": this.$store.state.access_token
            })
            this.http(this.configs.apiUl + "/user/certification/add&"+this.configs.display,"post",data,function(res) {
                let datas = res.data
                if(datas.c == 0){
                    Message({
                        message: '上传成功，等待审核结果',
                        type: 'success'
                    })
                    that.apply = false
                    that.success = true 
                }else{
                    Message({
                        message: datas.m,
                        type: "error"
                    })
                }
            })
        },
        again(){
            this.apply = true
            this.fail = false
        }
    },
    created () {
        let that = this
         this.http(this.configs.apiUl + "/user/certification/check?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
             let datas = res.data
             if(datas.c == 0){
                 that.user_certification_result = datas.d.user_certification_result
                 if(datas.d.is_submit == 'Y'){
                    if(datas.d.user_certification_status == "C"){//审核中
                        that.apply = false
                        that.success = true 
                    }else if(datas.d.user_certification_status == "N"){//审核失败
                        that.apply = false
                        that.success = false
                        // that.pass = false
                        that.fail = true
                    }else if(datas.d.user_certification_status == "Y"){//审核成功
                        that.success = false
                        that.pass = true
                    }
                 }
            }
         })
    }
}



