import { Message } from 'element-ui';
import $ from 'jquery'
export default {
    data () {
        return {
             dialogTableVisible:true,
             activeName:'first',
             veri: '',
             password: '',
             phone: '',
             num: 60,
             iscode: true
        }
    },
    watch: {
        dialogTableVisible () {
            this.$store.commit('hidwRegister')//隐藏登录框
            this.dialogTableVisible = true
        }
    },
    methods: {
        handleClick(tab,event){
            
        },
        code () {//获取验证码
            let that = this
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空！',
                    type: "error"
                })
                return false
            }
            if(this.iscode){
                this.http(this.configs.apiUl + "/pc/verifycode/forgetPwd?phone="+this.phone,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.iscode = false
                        Message({
                        message: '发送成功!',
                        type: 'success'
                        })
                        that.Interval = setInterval(function(){
                            that.num -= 1
                            if(that.num <= 0){
                                Message({
                                    message: '未收到验证码？点击重新发送'
                                })
                                that.num = 60
                                that.iscode = true
                                clearInterval(that.Interval)
                            }
                        },1000)
                    }else{
                        Message({
                            message: datas.m,
                            tyle: 'error'
                        })
                    }
                })
            }
        },
        submit () {
            let that = this
            if(this.phone == ""){
                Message({
                    message: '手机号不能为空',
                    type: "error"
                })
                return false
            }else if(this.password == ""){
                Message({
                    message: '密码不能为空！',
                    type: "error"
                })
                return false
            }else if(this.veri == ""){
                Message({
                    message: '验证码不能为空！',
                    type: "error"
                })
                return false
            }
            this.http(this.configs.apiUl + "/pc/login/resetPwd?phone="+this.phone+"&code="+this.veri+"&password="+this.password,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    Message({
                        message: '重置成功',
                        type: 'success'
                    })
                    setTimeout(function(){//弹出登录框
                        that.$store.state.isRegister = true
                        that.$store.state.isseekPwd = false
                    },2000)
                    clearInterval(that.Interval)
                }else{
                    Message({
                        message: datas.m,
                        tyle: 'error'
                    })
                }
            })
        }
        },
        mounted () {
            setTimeout(function() {
                 $("#pwd").focus()
            });
        },
        created () {

        }
}



