import vHeader from './public/header'
import vFooter from './public/footer'
import vBanner from './public/videobanner'
import Item from './public/item'
import { Message } from 'element-ui'
import $ from 'jquery'
import qs from 'qs'
export default {    
    data () {
      return {
        live: [],
        hot: [],
        user: {},
        isnotReal: false,
        headline:'',
        value:'',
        liveOne: true,
        push: false,
        liveLayer: false,
        imageUrl: '',
        live_category: [],
        live_item: [],
        anchorStart: {},
        categoryItem: {},
        arr: {},
        live_id: '',
        uploadUrl: this.configs.uploadUrl
      }
    },
    components: {
      vHeader,
      vFooter,
      vBanner,
      Item
    },
    watch:{
        live_category () {
            let that = this
            let leght = this.live_category.length
            var y = 0;
            for(var i=0;i<leght;i++){
               let id = that.live_category[i].anchor_category_id
               if(that.arr[id] == id){
                    this.http(this.configs.apiUl+"/pc/app/liveByCate?access_token="+this.$store.state.access_token+"&category_id="+ id +"&live_id="+ this.live_id +'&'+this.configs.display,'post','',function(res){
                        var datas = res.data
                        y++;
                        if(datas.c == 0){
                            that.categoryItem[id] = datas.d.items
                            if(y == leght){
                                 that.live_item = that.live_category
                            }             
                        }
                    })
               }
            }
        }
    },
   methods: {
       beforeUpload (file) {
           $('.no_word').text('上传中...')
            if(file.type == 'image/png' || file.type == 'image/jpeg'){

            }else{
                 this.$message.error('上传图片必须是 JPG/PNG 格式!');
                 $('.no_word').text('上传失败').css('color','red')
            }
       },
        notReal () {
            this.isnotReal = false
        },
        getBack () {//返回顶部
            window.scrollTo(0,0);
        },
        start () {//点击立即直播
            let that = this
            //获取个人信息
            this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                let datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                    if(datas.d.user_is_certification == 'N'){
                        that.isnotReal = true
                    }else{
                        that.http(that.configs.apiUl + "/live/anchor/getAnchorInfo?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&user_id="+datas.d.user_id,"get","",function(res) {
                            let user = res.data
                            if(user.d.anchor_is_live == 'Y'){
                                //  that.liveOne = false
                                //  that.push = true
                                //  that.liveLayer = true
                                // that.$router.push({path:'/anchorLive'}) 
                                window.open('/anchorLive')   
                            }else{
                                that.liveLayer = true
                            }
                        })
                    }
                }else{
                    localStorage.setItem('access_token','')//localStorage清除用户登录信息
                    that.$store.state.access_token = ""//vuex清除用户登录信息
                    that.$store.commit('showRegister')//显示登录框
                }
            })
        },
        userstart () {//开始直播
            let that = this
            if(this.value == ""){
                Message({
                    message: '请选择直播分类',
                    type: "error"
                })
                return false
            }
            let data = qs.stringify({
                'access_token': this.$store.state.access_token,
                "anchor_category_id": this.value,
                "anchor_lat": 0,
                "anchor_live_img": this.imageUrl,
                "anchor_live_title": this.headline,
                "anchor_lng": 0,
                "anchor_local": '北京',
                'display': 'pc',
                "anchor_live_pay": 0
            })
            this.http(this.configs.apiUl + "/live/anchor/startLive","post", data,function(res) {
                let datas = res.data
                if(datas.c == 0){
                    // console.log(datas)
                    that.liveOne = false
                    that.push = true
                    that.anchorStart = datas.d
                    var url = that.anchorStart.push_url
                    var k = that.anchorStart.key
                    that.anchorStart.push_url = url.substring(0,url.indexOf(k)-1)
                }
            })
        },
        close(){
            this.liveLayer = false
        },
        handleAvatarSuccess (response, file, fileList) {//上传直播封面
           if(response.d.url){
                this.imageUrl = response.d.url
           }
            
        },
        classify (id) {//分类列表详情
            let that = this  
            that.http(that.configs.apiUl+"/pc/app/liveByCate?access_token="+that.$store.state.access_token+"&category_id="+ id +'&'+that.configs.display,'post','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    that.categoryItem = datas.d.items
                    return that.categoryItem
                }
            })
        }
   },
    created(){
        let that = this    
        //获取最新直播跟回放
         this.http(this.configs.apiUl + "/pc/app/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            let datas = res.data
            if(datas.c == 0){
                // if(datas.d.live.length == 0){
                //     that.live = datas.d.play_back
                // }else{
                //     that.live = datas.d.live
                // }
                 that.live = datas.d.live
                if(datas.d.live.length < 5){
                    var len = 5 - datas.d.live.length
                    var arry = datas.d.play_back.slice(0,len)
                    that.live.push(...arry)
                }else{
                    that.live = datas.d.live
                }





            }
            for(var i=0;i<that.live.length;i++){
                if(i < that.live.length-1){
                    that.live_id += that.live[i].user_id+","
                }else{
                    that.live_id += that.live[i].user_id
                }
            }
            //获取热门直播
            that.http(that.configs.apiUl + "/pc/app/hot?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&live_id="+ that.live_id,"get","",function(res) {
                let datas = res.data
                if(datas.c == 0){
                    that.hot = datas.d.items
                }
                 that.live_id =  that.live_id+ ','
                for(var i=0;i<that.hot.length;i++){
                    if(i < that.hot.length-1){
                        that.live_id += that.hot[i].user_id+","
                    }else{
                        that.live_id += that.hot[i].user_id
                    }
                }
                //获取分类列表
                that.http(that.configs.apiUl+"/pc/app/liveclass?access_token="+that.$store.state.access_token+"&"+that.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.live_category = datas.d.live_category
                        for(var i=0;i<that.live_category.length;i++){
                            that.arr[that.live_category[i].anchor_category_id] = that.live_category[i].anchor_category_id
                            // that.categoryItem[that.live_category[i].anchor_category_id] = ''
                        }
                    }
                }) 
            })
         })
    }
}



