export default {
    props: ['index','userid'],
    data () {
        return {
            selfTro:[
                {img: '/static/images/01zbohuifang.png',name:'我的直播回放',index:'userHome'},
                {img: '/static/images/paihangbnag.png',name:'雨音排行榜',index:'bang'},
                {img: '/static/images/xiaoxi.png',name:'我的消息',index:'privateMsg'},
                {img: '/static/images/huiyuan.png',name:'我的会员',index:'member'},
                {img: '/static/images/fangguang.png',name:'我的房管',index:'manageroom'},
                {img: '/static/images/shoyi.png',name:'我的收益',index:'earn'},
                {img: '/static/images/xiaofei.png',name:'我的消费',index:'spend'},
                {img: '/static/images/chongzhi.png',name:'我的充值',index:'recharge'},
                {img: '/static/images/tixian.png',name:'我的提现',index:'withdraw'},
                {img: '/static/images/shezhi.png',name:'设置',index:'set'},
            ],
            user: {},
            isUser: true
        }
    },
    methods: {
        userProfile () {//获取个人信息
            let that = this
            this.http(this.configs.apiUl + "/user/profile/card?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.userid,"get","",function(res) {//获取用户信息
                let datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                    if(that.user.user_sex == '1'){
                        that.user.user_sex = '男'
                    }else{
                        that.user.user_sex = '女'
                    }
                    if(Number(that.user.user_follow_total) >= 10000){
                        that.user.user_follow_total = (Number(that.user.user_follow_total) /10000).toFixed(2) +'万'
                    }
                    if(Number(that.user.user_fans_total) >= 10000){
                        that.user.user_fans_total = (Number(that.user.user_fans_total) /10000).toFixed(2) +'万'
                    }
                }
            })
        }
    },
    created () {
        let that = this
         //判断是否用户
        if(that.userid != undefined){
                that.userProfile()
                that.isUser = false
                return false
        }
        this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {//获取用户信息
                var datas = res.data
                if(datas.c == 0){
                    that.user = datas.d
                    if(that.user.user_sex == '1'){
                        that.user.user_sex = '男'
                    }else{
                        that.user.user_sex = '女'
                    }
                    if(Number(that.user.user_follow_total) >= 10000){
                        that.user.user_follow_total = (Number(that.user.user_follow_total) /10000).toFixed(2) +'万'
                    }
                    if(Number(that.user.user_fans_total) >= 10000){
                        that.user.user_fans_total = (Number(that.user.user_fans_total) /10000).toFixed(2) +'万'
                    }

                    /**
                    //判断是否用户
                    if(that.userid != undefined){
                        if(that.userid == that.user.user_id){
                            that.isUser = true
                        }else{
                            that.userProfile()
                            that.isUser = false
                        }
                    }else{
                        that.isUser = true
                    }
                     */
                    
                }     
        })
    }
}



