import vHeader from './public/header'
import vVideo from './public/video'
import Item from './public/item'
import vFooter from './public/footer'
export default {
    data () {
        return {
            userid: '',
            anchor: {},
            live:{},
            expre: false,
            anchorData: {},
            user: {}
        }
    },
    components: {
        vHeader,
        vVideo,
        Item,
        vFooter
    },
    watch: {
        userid () {
            let that = this
            //获取主播信息
            this.http(this.configs.apiUl+"/live/anchor/getAnchorInfo?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+this.userid,'get','',function(res){
                let datas = res.data
                if(datas.c==0){
                    that.anchorData = datas.d
                    that.userid = datas.d.user_id
                    if(that.anchorData.user_sex == '1'){
                        that.anchorData.user_sex = "/static/images/nan.png"
                    }else if(that.anchorData.user_sex == '2'){
                        that.anchorData.user_sex = "/static/images/nv.png"
                    }
                    that.http(that.configs.apiUl+"/live/anchor/getInfo?user_id="+that.userid,'get','',function(res){
                        let datas = res.data
                            if(datas.c==0){
                                that.anchorData.live_notice = datas.d.anchor.live_notice
                            }
                    })
                }
            })

        }
    },
    methods: {
        liveRoom () {
            let that = this
            //观众进入直播间
            this.http(this.configs.apiUl+"/live/room/enter?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&anchor_user_id="+this.userid,'get','',function(res){
                let datas = res.data
                if(datas.c==0){
                    that.anchor = datas.d.anchor
                    that.user = datas.d.user
                    that.live = datas.d.live
                }
            })

        }
        
    },
    created () {
        let that = this
        //获取个人信息
         this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            let datas = res.data
            if(datas.c==0){
                // that.anchorData = datas.d
                that.userid = datas.d.user_id
            }
         })
        setInterval(() =>{
            this.http(this.configs.apiUl+"/live/anchor/heartbeat?access_token="+this.$store.state.access_token+'&'+this.configs.display,'get','',function(res){
                // console.log('心跳')
            }) 
        },2000)
    }
}



