
import Qrcode from 'vux/src/components/qrcode/index.vue'

import vHeader from './public/header'
import vFooter from './public/footer'
export default {
data () {
return {
text: '微信扫码',
money:'',
url: '',
name: '/member'
}
},
components: {
vHeader,
vFooter,
Qrcode
},
methods: {
to() {//返回上一页
this.$router.go(-1)
}
},
created () {
this.name = this.$route.query.name 
this.money = this.$route.query.money
this.url = this.$route.query.url 
}
}



