import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import $ from 'jquery'
export default {
    data () {
        return {
           money:[],
           user: {},
           type: '',
           order_id: '',
           paytype: '1',
           moneyText: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    methods: {
        payType(obj){
            if(obj == 1){
                this.paytype = 1;
                $(".zzfb").addClass("xz");
                $(".wwx").removeClass("xz");
            }else if(obj == 2){
                this.paytype = 2;
                $(".wwx").addClass("xz");
                $(".zzfb").removeClass("xz");
            }
        },
        time (value) {
             function zeroize( num ) {
                return (String(num).length == 1 ? '0' : '') + num;
            }
            var tmDate = new Date( value * 1000 );  // 参数时间戳转换成的日期对象
            var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
            var H = tmDate.getHours(), i = tmDate.getMinutes(), s = tmDate.getSeconds();
            return  Y + '-'+ zeroize(m) + '-' + zeroize(d);
         },
         member_money (id, moneyText){
             this.type = id
             this.moneyText = moneyText
         },
         kt () {//开通会员
            let that= this
            if(this.paytype == 1){//支付宝

                location.href = this.configs.apiUl + "/user/vip/recharge?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&combo_id="+this.type+"&trade_type=NATIVE&pay_type=1"
            }else if(this.paytype ==  2){//微信
                this.http(this.configs.apiUl + "/user/vip/recharge?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&combo_id="+this.type+"&pay_type=2&trade_type=NATIVE", "get", "",function(res){
                   let datas = res.data 
                   //var url = 'weixin://wxpay/bizpayurl?sign='+datas.d.sign+'&appid='+datas.d.appid+'&mch_id='+datas.d.partnerid+'&product_id='+datas.d.prepayid+'&time_stamp='+datas.d.timestamp+'&nonce_str='+datas.d.noncestr  
                    if(datas.c == 0){
                        //跳转支付页面
                        that.$router.push({path:"/payWay",query:{"name":"member",'url':datas.d.code_url,'money': that.moneyText}})
                    }
                })
            }
            
            // this.http(this.configs.apiUl + "/user/vip/recharge?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&combo_id="+this.type+"&order_id="+this.order_id,"get","",function(res) {
            //     let datas = res.data
            //     console.log(datas)
            //     if(datas.c == 0){
            //          that.$message({
            //             message: '充值成功',
            //             type: 'success'
            //         })
            //         that.user = datas.d.user
            //     }else{
            //         that.$message({
            //             message: datas.m,
            //             type: 'error'
            //         })
            //     }
            // })
         }
    },
    created () {
        let that= this
        //获取列表信息
        this.http(this.configs.apiUl + "/user/vip/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            let datas = res.data
            if(datas.c == 0){
                that.user = datas.d.user
                that.order_id = datas.d.order_id
                that.money = datas.d.vip_combo
                that.type = that.money[0].combo_id
                that.moneyText = that.money[0].combo_fee
            }
        })
    }
}



